import { RM } from '@speedlo/tools'
import { useXField, useXFormState, XFieldName } from '@speedlo/xform'
import { useObserver } from 'mobx-react-lite'
import React from 'react'

import { ISelectControlProps, SelectControl } from '../../atoms/SelectControl'

type DefaultOptionType = object

export interface FormDropdownProps<
  OptionType extends DefaultOptionType = DefaultOptionType
> extends ISelectControlProps<OptionType> {
  fieldName: XFieldName
}

export const FormDropdown = <OptionType extends DefaultOptionType>({
  fieldName,
  ...props
}: FormDropdownProps<OptionType>) => {
  const field = useXField(fieldName)
  const { isMulti, options, getOptionValue } = props

  const getPropsForMulti = () => {
    const selection = field.value
    const isSelected = Array.isArray(selection)
      ? (option: OptionType) => selection.includes(getOptionValue!(option))
      : RM.F

    return {
      value: Array.isArray(options)
        ? (options as OptionType[]).filter(isSelected)
        : [],
      onChange: selectedOptions => {
        field.setValue(selectedOptions.map(getOptionValue))
      },
    }
  }

  const getPropsForSingle = () => {
    const selection = field.value
    return {
      value:
        selection &&
        (options as OptionType[]).find(
          item => getOptionValue!(item) === selection,
        ),
      onChange: option => {
        field.setValue(getOptionValue!(option))
      },
    }
  }

  const formState = useXFormState()

  return useObserver(() => (
    <SelectControl
      {...(isMulti ? getPropsForMulti() : getPropsForSingle())}
      onFocus={() => field.setTouched()}
      invalid={field.touched && field.hasError}
      isDisabled={formState.isSubmitting}
      {...props}
    />
  ))
}
