import { AddCircle } from '@material-ui/icons'
import { RM } from '@speedlo/tools'
import { useXField, useXFieldState, useXFormState } from '@speedlo/xform'
import { useObserver } from 'mobx-react-lite'
import React, { Fragment } from 'react'
import styled from 'styled-components'

import { i18n } from '../../../i18n'
import { TouchButton } from '../../../molecules/TouchButton'
import { TouchButtonDelete } from '../../../molecules/TouchButtonDelete'
import { Colors, Spacing } from '../../../styling'
import { ZoneFormRuleInputs } from '../atoms/ZoneFormRuleInputs'
import { ZoneFormToPrice } from '../atoms/ZoneFormToPrice'
import { TZoneFormValues } from '../zones.types'

interface IProps {}

const StyledZoneFormPriceRules = styled.div`
  display: grid;

  grid-template-columns: 4.5rem 7rem 8rem 4.5rem;

  & input {
    width: 4rem;
    z-index: 1;
  }
  & label {
    margin: ${Spacing.Middle};
  }

  & .minimalPriceField {
    grid-column: 2;
    display: flex;
    align-items: center;
  }
  & .minimalPriceField span {
    margin: ${Spacing.Middle};
  }
  & .addRule {
    grid-column: 1;
    justify-self: start;
    align-self: center;
    margin-bottom: 3rem;
  }
  & .toPriceField {
    grid-column: 2;
    display: flex;
    align-items: center;
    align-self: end;
    position: relative;
  }
  & .toPriceField span {
    margin: ${Spacing.Middle};
  }
  & .forPriceField {
    grid-column: 3;
    display: flex;
    align-items: center;
    align-self: center;
    margin-bottom: 3rem;
  }
  & .forPriceField input {
    color: ${Colors.blue};
    width: 4rem;
  }
  & .forPriceField span {
    margin: ${Spacing.Middle};
  }
  & .removeRule {
    height: 5rem;
    margin-bottom: 3rem;
    grid-column: 4;
    display: flex;
    align-items: center;
    justify-self: end;
    align-self: center;
  }
  & .verticalLine {
    width: 3px;
    height: 8rem;
    background: ${Colors.greyMiddle};
    position: absolute;
    top: -112px;
    left: 62px;
  }
`

export const ZoneFormPriceRules: React.FC<IProps> = () => {
  const rulesOrderField = useXField<TZoneFormValues['priceRulesOrder']>(
    'priceRulesOrder',
  )
  const rulesField = useXFieldState<TZoneFormValues['priceRules']>('priceRules')
  const state = useXFormState()

  const [focusId, setFocusId] = React.useState<MaybeID>(null)

  // artificial ID for a dynamic changes of array
  const idCounter = React.useRef(rulesOrderField.value.length)

  React.useEffect(() => {
    if (idCounter.current < rulesOrderField.value.length) {
      idCounter.current = rulesOrderField.value.length
    }
  }, [rulesOrderField.value])

  const addRule = (afterRule: ID) => {
    const idx = rulesOrderField.value.findIndex(RM.equals(afterRule))
    const ruleId = String(++idCounter.current)
    rulesOrderField.value.splice(idx, 0, ruleId)
    rulesField.value[ruleId] = {
      clientId: String(ruleId),
      toPrice: null,
      price: null,
    }
    setFocusId(ruleId)
  }

  const removeRule = (rule: ID) => {
    const idx = rulesOrderField.value.findIndex(RM.equals(rule))
    rulesOrderField.value.splice(idx, 1)
    delete rulesField.value[rule]
  }

  return useObserver(() => (
    <StyledZoneFormPriceRules>
      <div className="minimalPriceField">
        <ZoneFormToPrice fieldName={['minimalPrice']} />
      </div>
      {rulesOrderField.value.map((ruleId, idx, rules) => (
        <Fragment key={ruleId}>
          <TouchButton
            className="addRule"
            disabled={state.isSubmitting}
            width={Spacing.ButtonHeight + Spacing.DefaultSizeUnit}
            height={Spacing.ButtonHeight + Spacing.DefaultSizeUnit}
            onClick={() => addRule(ruleId)}
            icon={AddCircle}
            title={i18n.t`Add another range`}
          />
          <ZoneFormRuleInputs
            fieldPath={['priceRules', ruleId]}
            shouldFocus={ruleId === focusId}
            isLastRule={idx === rules.length - 1}
          />
          <div className="removeRule">
            <TouchButtonDelete
              onDelete={() => removeRule(ruleId)}
              title={i18n.t`Remove price range`}
              disabled={rules.length <= 1}
            />
          </div>
        </Fragment>
      ))}
    </StyledZoneFormPriceRules>
  ))
}
