import { Polygon } from '@react-google-maps/api'
import React from 'react'

import { ZoneTypeEnum } from '../../../graph/generated'
import { gpsToLatLng } from '../../../helpers/gps'

interface IZoneMapAreaProps {
  points: RoA<GeoPoint>
  type: ZoneTypeEnum
  color: string
  priority: number
  onFocus?(): void
}

export const ZoneMapArea: React.FC<IZoneMapAreaProps> = ({
  points,
  type,
  color,
  priority,
  onFocus,
}) => {
  const onAreaFocus = React.useCallback(
    (e: google.maps.PolyMouseEvent) => {
      if (onFocus) {
        e.stop()
        onFocus()
      }
    },
    [onFocus],
  )

  const getOptions = (): google.maps.PolygonOptions => {
    if (type === ZoneTypeEnum.Return) {
      return {
        fillColor: 'transparent',
        strokeWeight: 3,
        strokeColor: color,
        zIndex: 10000 - priority,
      }
    }
    return {
      fillColor: color,
      fillOpacity: 0.3,
      strokeWeight: 0,
      zIndex: 5000 - priority,
    }
  }

  return (
    <Polygon
      path={points.map(gpsToLatLng)}
      options={getOptions()}
      editable={false}
      draggable={false}
      onDblClick={onAreaFocus}
    />
  )
}
