import { OverlayView } from '@react-google-maps/api'
import React from 'react'
import styled, { css } from 'styled-components'

import { FCompanyBranchFragment } from '../graph/generated'
import { gpsToLatLng } from '../helpers/gps'
import { Colors } from '../styling'

interface IProps {
  branch: FCompanyBranchFragment
  isActive?: boolean
}

const BranchNameStyled = styled.div`
  ${getColors} font-size: 1.3rem;
  white-space: nowrap;
  padding: 0.2rem 0.5rem;
`

export const MapCompanyBranchMarker: React.FC<IProps> = ({
  branch,
  isActive = true,
}) => (
  <OverlayView
    getPixelPositionOffset={(width, height) => ({
      x: -(width / 2),
      y: -(height / 2),
    })}
    mapPaneName={OverlayView.MARKER_LAYER}
    position={gpsToLatLng(branch.gps)}
  >
    <BranchNameStyled isActive={isActive}>{branch.name}</BranchNameStyled>
  </OverlayView>
)

function getColors({ isActive }: { isActive: boolean }) {
  return css`
    background-color: ${isActive ? Colors.green : Colors.greyLight};
    color: ${isActive ? Colors.white : Colors.greyDark};
  `
}
