import { AddCircle } from '@material-ui/icons'
import { observer } from 'mobx-react-lite'
import React from 'react'

import { useMZoneCreate, ZoneTypeEnum } from '../../../graph/generated'
import { generatePolygon } from '../../../helpers/gps'
import { useSnackBar } from '../../../hooks/useSnackBar'
import { i18n } from '../../../i18n'
import { TouchButton } from '../../../molecules/TouchButton'
import { Colors, Spacing } from '../../../styling'
import { useZoneModel } from '../hooks/useZoneModel'
import { useZoneRouteControl } from '../hooks/useZoneRouteControl'

interface IProps {
  type: ZoneTypeEnum
}

export const CreateZoneButton = observer<IProps>(({ type }) => {
  const zoneModel = useZoneModel()
  const { routeToZoneEdit } = useZoneRouteControl()

  const [createZone] = useMZoneCreate()
  const { enqueueSuccess } = useSnackBar()

  const [disabled, setDisabled] = React.useState(false)

  const onClick = async () => {
    if (!zoneModel.branch) {
      return
    }
    setDisabled(true)
    const result = await createZone({
      type,
      branchId: zoneModel.branch.id,
      points: generatePolygon({
        center: zoneModel.branch.gps,
        metersRadius: 1000,
        pointsCount: 5,
      }),
    })
    if (result) {
      enqueueSuccess(i18n.t`Zone has been created`)
      routeToZoneEdit(result.createZone.id)
    }
  }

  const createTextByType = {
    [ZoneTypeEnum.Area]: i18n.t`Create delivery zone for branch`,
    [ZoneTypeEnum.Return]: i18n.t`Create return zone for branch`,
  }

  const colorByType = {
    [ZoneTypeEnum.Area]: Colors.turquoise,
    [ZoneTypeEnum.Return]: Colors.greyMiddle,
  }

  const text = zoneModel.hasBranchFilter
    ? createTextByType[type]
    : i18n.t`Select branch to create zone`

  return (
    <TouchButton
      text={text}
      onClick={onClick}
      disabled={disabled || !zoneModel.hasBranchFilter}
      color={colorByType[type]}
      height={Spacing.ButtonHeight + Spacing.DefaultSizeUnit}
      textNextToIcon
      icon={AddCircle}
    />
  )
})
