import React from 'react'
import { RouteComponentProps } from 'react-router-dom'

import { i18n } from '../../../i18n'
import { PageLayoutFullHeightItem } from '../../../molecules/PageLayoutFullHeightItem'
import { PageLayoutResponsive } from '../../../molecules/PageLayoutResponsive'
import { ROUTE_SETTINGS } from '../../settings/settings.routes'
import { ZoneFormLoader } from '../molecules/ZoneFormLoader'
import { ZoneMap } from '../molecules/ZoneMap'
import { ZoneFormPanel } from '../organisms/ZoneFormPanel'
import { ZoneMenuPanel } from '../organisms/ZoneMenuPanel'

interface IProps extends RouteComponentProps<{ zoneId?: ID }> {}

export const ZonesOverviewPage: React.FC<IProps> = ({ match }) => {
  const zoneId = match.params.zoneId || null
  const isEditing = Boolean(zoneId)
  return (
    <PageLayoutResponsive
      pageTitle={i18n.t`Delivery zones`}
      backRoute={ROUTE_SETTINGS}
      useBackButton
    >
      <ZoneFormLoader zoneId={zoneId}>
        <PageLayoutFullHeightItem
          widthInColumns={6}
          column={10}
          rowInMobile={2}
          withScrollArea={true}
        >
          {isEditing ? <ZoneFormPanel zoneId={zoneId} /> : <ZoneMenuPanel />}
        </PageLayoutFullHeightItem>
        <PageLayoutFullHeightItem
          widthInColumns={10}
          rowInMobile={1}
          forcePercentHeightInMobile={60}
        >
          <ZoneMap zoneId={zoneId} />
        </PageLayoutFullHeightItem>
      </ZoneFormLoader>
    </PageLayoutResponsive>
  )
}
