import { t } from '@lingui/macro'
import { XFieldName } from '@speedlo/xform'
import { observer } from 'mobx-react-lite'
import React from 'react'

import { StyledFormInput } from '../../../atoms/FormInputStyled'
import { FormDisplayValue } from '../../../components/forms/FormDisplayValue'
import { useFormInputProps } from '../../../components/forms/FormInput'
import { i18n } from '../../../i18n'

interface IProps {
  fieldName: XFieldName
}

export const ZoneFormForPrice = observer<IProps, HTMLInputElement>(
  ({ fieldName }, ref) => {
    const fieldInputProps = useFormInputProps({
      fieldName,
    })()

    return (
      <>
        <label className="forPriceLabel" htmlFor={fieldInputProps.id}>
          {i18n._(
            /*i18n: Label for input with transport fee used when zone rule applies */ t`for`,
          )}
        </label>
        <StyledFormInput
          {...fieldInputProps}
          className="forPrice"
          ref={ref}
          inputFullWidth={true}
        />
        <FormDisplayValue fieldName="currencyCode" />
      </>
    )
  },
  { forwardRef: true },
)
