import { useAutorun } from '@speedlo/hooks'
import { idPropEq } from '@speedlo/tools'
import { getCenter } from 'geolib'
import { useAsObservableSource } from 'mobx-react-lite'
import React from 'react'

import { useZoneModel } from '../hooks/useZoneModel'
import { useZoneListQuery } from './useZoneListQuery'

export function useZoneMapControl(zoneId: MaybeID) {
  const zoneModel = useZoneModel()
  const { zones } = useZoneListQuery()

  const branches = zoneModel.branches.filter(zoneModel.branchFilterPredicate)

  const isActive = React.useCallback(
    (companyBranchId: ID) => {
      return (
        !zoneModel.hasBranchFilter ||
        companyBranchId === zoneModel.branchFilterId
      )
    },
    [zoneModel],
  )

  const source = useAsObservableSource({ zoneId, zones })

  // when editing zone, pan map to branch with that zone
  useAutorun(() => {
    if (zoneModel.map.isReady && source.zoneId && source.zones) {
      const zone = source.zones.find(idPropEq(source.zoneId))
      if (zone) {
        const center = getCenter(zone.geoPolygon as GeoPoint[])
        zoneModel.map.panTo(center as GeoPoint)
      }
    }
  })

  // when branch changes and we are not editing zone, pan to branch gps
  useAutorun(() => {
    if (zoneModel.map.isReady && zoneModel.branch && !source.zoneId) {
      zoneModel.map.panTo(zoneModel.branch.gps)
    }
  })

  return { isActive, branches, zones }
}
