import React from 'react'
import styled from 'styled-components/macro'

import { mediaDesktop, mediaMobile, useResponsive } from '../app/Responsive'
import { ScrollArea } from '../atoms/ScrollArea'
import { Sizer } from '../atoms/Sizer'
import { Spacing } from '../styling'

interface IPageLayoutFullHeightItemProps {
  row?: number
  column?: number
  rowInMobile?: number
  widthInColumns?: number
  withScrollArea?: boolean
  forcePercentHeightInMobile?: number
  scrollAreaId?: string
}

const gridColumnWidth: string = '6.22vw'
const gridColumnHeigth: string = '3.9375rem'

const leftPos = () => (props: IPageLayoutFullHeightItemProps) => {
  if (props.column !== undefined) {
    return (
      'calc((' +
      props.column +
      ' * ' +
      gridColumnWidth +
      ') + ' +
      Spacing.LayoutGridCellMargin +
      ')'
    )
  } else {
    return Spacing.LayoutGridCellMargin
  }
}
const widthComputed = () => (props: IPageLayoutFullHeightItemProps) => {
  if (props.widthInColumns !== undefined) {
    return (
      'calc((' +
      props.widthInColumns +
      ' * ' +
      gridColumnWidth +
      ') - ' +
      Spacing.LayoutGridCellMargin +
      ')'
    )
  } else {
    return 'calc(100vh - (2 * ' + Spacing.LayoutGridCellMargin + '))'
  }
}
const topPos = () => (props: IPageLayoutFullHeightItemProps) => {
  if (props.row !== undefined) {
    return (
      'calc((' +
      props.row +
      ' * ' +
      gridColumnHeigth +
      ') + ' +
      Spacing.LayoutGridCellMargin +
      ')'
    )
  } else {
    return (
      'calc(' + gridColumnHeigth + ' + ' + Spacing.LayoutGridCellMargin + ')'
    )
  }
}

// eslint-disable-next-line no-unexpected-multiline
const StyledPageLayoutFullHeightItem = styled.div<
  IPageLayoutFullHeightItemProps
>`
  ${mediaDesktop`
  position: fixed;
  bottom: 0.5rem;
  width: ${widthComputed()};
  left: ${leftPos()};
  top: ${topPos()};`}

  ${mediaMobile`
    margin: 0;
    grid-row: ${({ rowInMobile }: IPageLayoutFullHeightItemProps) =>
      rowInMobile} ;
    grid-column: span 16;
    height: ${({
      forcePercentHeightInMobile,
    }: IPageLayoutFullHeightItemProps) =>
      forcePercentHeightInMobile
        ? `calc(${forcePercentHeightInMobile}vh - ${Spacing.PageHeaderHeight +
            0.25 +
            Spacing.DefaultSizeUnit})`
        : 'unset'} ;
    position: relative;
  `}
`

export const PageLayoutFullHeightItem: React.FC<
  IPageLayoutFullHeightItemProps
> = ({
  children,
  row = 1,
  rowInMobile = 16,
  column = 0,
  widthInColumns = 16,
  withScrollArea = false,
  scrollAreaId,
  forcePercentHeightInMobile: forceHeightInMobile,
}) => {
  const { isMobile } = useResponsive()

  return (
    <StyledPageLayoutFullHeightItem
      column={column}
      row={row}
      rowInMobile={rowInMobile}
      widthInColumns={widthInColumns}
      withScrollArea={withScrollArea}
      forcePercentHeightInMobile={forceHeightInMobile}
    >
      {withScrollArea && !isMobile ? (
        <ScrollArea id={scrollAreaId}>{children}</ScrollArea>
      ) : (
        <Sizer height={100} heightUnit="%">
          {children}
        </Sizer>
      )}
    </StyledPageLayoutFullHeightItem>
  )
}
