import { Trans } from '@lingui/react'
import { useObserver } from 'mobx-react-lite'
import React from 'react'
import styled from 'styled-components'

import { CanMutate } from '../../../atoms/CanMutate'
import { Card } from '../../../atoms/Card'
import { UserHasMultipleBranches } from '../../../atoms/UserHasMultipleBranches'
import { ZoneTypeEnum } from '../../../graph/generated'
import { useAutorunWhenSingleBranch } from '../../../hooks/useAutorunWhenSingleBranch'
import { ZoneListSection } from '../atoms/ZoneListSection'
import { useZoneModel } from '../hooks/useZoneModel'
import { CreateZoneButton } from '../molecules/CreateZoneButton'
import { ZoneList } from '../molecules/ZoneList'
import { ZoneFilter } from './ZoneFilter'

const StyledZoneMenuPanel = styled.div`
  display: flex;
  flex-direction: column;
  height: 100%;

  & div.header {
    flex: 0 0 auto;
  }

  & div.zone-list {
    flex: 1 1 0;
  }

  & h3 {
    margin-top: 6rem;
  }
`

export const ZoneMenuPanel = React.memo(() => {
  const zoneModel = useZoneModel()

  useAutorunWhenSingleBranch(branch => {
    zoneModel.setBranchFilter(branch)
  })

  return useObserver(() => (
    <StyledZoneMenuPanel>
      <div className="header">
        <UserHasMultipleBranches>
          <Card>
            <ZoneFilter />
          </Card>
        </UserHasMultipleBranches>
      </div>

      <div className="zone-list">
        {zoneModel.zoneTypes.map((type, idx) => (
          <div className={`list${idx}`}>
            {type === ZoneTypeEnum.Return && (
              <h3>
                <Trans>Service zones</Trans>
              </h3>
            )}
            <CanMutate mutationName="createZone">
              <CreateZoneButton type={type} />
            </CanMutate>
            <ZoneListSection type={type} />

            <ZoneList type={type} />
          </div>
        ))}
      </div>
    </StyledZoneMenuPanel>
  ))
})
