import { Polygon } from '@react-google-maps/api'
import React from 'react'

import { ZoneTypeEnum } from '../../../graph/generated'
import { gpsToLatLng, latLngToGps } from '../../../helpers/gps'

interface IProps {
  type: ZoneTypeEnum
  color: string
  initialPoints: RoA<GeoPoint>
  onChange(points: GeoPoint[]): void
}

export const ZoneMapEditableArea: React.FC<IProps> = ({
  type,
  color,
  onChange,
  initialPoints,
}) => {
  const polygonRef = React.useRef<google.maps.Polygon | null>(null)
  const listenersRef = React.useRef<google.maps.MapsEventListener[]>([])

  const onAreaChange = React.useCallback(() => {
    if (polygonRef.current) {
      const path = polygonRef.current.getPath()
      onChange(path.getArray().map(latLngToGps))
    }
  }, [onChange])

  const onLoad = React.useCallback(
    (polygon: google.maps.Polygon) => {
      polygonRef.current = polygon
      const path = polygon.getPath()
      listenersRef.current.push(
        path.addListener('set_at', onAreaChange),
        path.addListener('insert_at', onAreaChange),
        path.addListener('remove_at', onAreaChange),
      )
    },
    [onAreaChange],
  )

  const onUnmount = React.useCallback(() => {
    listenersRef.current.forEach(lis => lis.remove())
    listenersRef.current.length = 0
    polygonRef.current = null
  }, [])

  const initialPointsRef = React.useRef(initialPoints)
  const initialPath = React.useMemo(
    () => initialPointsRef.current.map(gpsToLatLng),
    [],
  )

  const getOptions = (): google.maps.PolygonOptions => {
    if (type === ZoneTypeEnum.Return) {
      return {
        fillColor: 'transparent',
        strokeWeight: 5,
        strokeColor: color,
        zIndex: 20000,
      }
    }
    return {
      fillColor: color,
      fillOpacity: 0.1,
      strokeWeight: 2,
      zIndex: 20000,
    }
  }

  return (
    <Polygon
      path={initialPath}
      options={getOptions()}
      editable={true}
      draggable={true}
      onDragEnd={onAreaChange}
      onLoad={onLoad}
      onUnmount={onUnmount}
    />
  )
}
