import { useXFieldState, useXFormState } from '@speedlo/xform'
import { observer } from 'mobx-react-lite'
import React from 'react'
import Goodbye from 'react-goodbye'
import styled from 'styled-components'

import { CanMutate } from '../../../atoms/CanMutate'
import { Card } from '../../../atoms/Card'
import { ConfirmDialog } from '../../../atoms/ConfirmDialog'
import { useMZoneDelete } from '../../../graph/generated'
import { i18n } from '../../../i18n'
import { TouchButtonDelete } from '../../../molecules/TouchButtonDelete'
import { Spacing } from '../../../styling'
import { useZoneRouteControl } from '../hooks/useZoneRouteControl'
import { ZoneFormContent } from '../molecules/ZoneFormContent'
import { ZoneFormControls } from '../molecules/ZoneFormControls'
import { TZoneFormValues } from '../zones.types'

const DeleteButtonContainer = styled.div`
  width: ${Spacing.ButtonHeight + Spacing.DefaultSizeUnit};
  margin-top: ${Spacing.Middle};
`

interface IProps {
  zoneId: MaybeID
}

export const ZoneFormPanel = observer<IProps>(({ zoneId }) => {
  const { routeToZoneList } = useZoneRouteControl()

  const [deleteZone] = useMZoneDelete()

  const onDelete = React.useCallback(async () => {
    if (zoneId) {
      await deleteZone({ zoneId })
    }
    routeToZoneList()
  }, [deleteZone, routeToZoneList, zoneId])

  const state = useXFormState<TZoneFormValues>()

  const { value: type } = useXFieldState('type')

  return (
    <Card padding={0}>
      <ZoneFormControls />
      <ZoneFormContent type={type} />

      <CanMutate mutationName="destroyZone">
        <DeleteButtonContainer>
          <TouchButtonDelete
            disabled={state.isSubmitting}
            confirmText={i18n.t`Remove zone ${state.values.name}?`}
            onDelete={onDelete}
          />
        </DeleteButtonContainer>
      </CanMutate>

      <Goodbye when={state.isDirty}>
        {({ isShow, handleOk, handleCancel }) => (
          <ConfirmDialog
            isShow={isShow}
            confirmText={i18n.t`Do you want to cancel changes for the zone?`}
            onConfirm={handleOk}
            onCancel={handleCancel}
          />
        )}
      </Goodbye>
    </Card>
  )
})
