import { idProp } from '@speedlo/tools'

import { useRoot } from '../../../atoms/RootProvider'
import { useQZoneList } from '../../../graph/generated'
import { useLoadingCache } from '../../../hooks/useLoadingCache'
import { TZoneInList } from '../zones.types'

export function useZoneListQuery() {
  const { zone } = useRoot()
  const branches = zone.branches.filter(zone.branchFilterPredicate).map(idProp)
  const skip = branches.length === 0

  const { data, loading } = useQZoneList({ branches }, { skip })

  const getZones = useLoadingCache<Maybe<RoA<TZoneInList>>>(skip)
  const zones = getZones(loading, () => (skip ? null : data.zones))

  return { zones, loading }
}
