import React from 'react'
import { SortableContainer, SortEndHandler } from 'react-sortable-hoc'
import styled from 'styled-components/macro'

import { ZoneTypeEnum } from '../../../graph/generated'

interface IProps {
  type: ZoneTypeEnum
  children: ReactNodes
  onSort: SortEndHandler
}

const ZoneListContainerStyled = styled.div`
  margin-top: 0.5rem;
  cursor: arrow;
`

const SortableZoneList = SortableContainer(ZoneListContainerStyled)

export const ZoneListCardGroup: React.FC<IProps> = ({
  type,
  onSort,
  children,
}) => {
  return (
    <SortableZoneList
      onSortEnd={onSort}
      useDragHandle
      lockAxis="y"
      lockToContainerEdges
    >
      {children}
    </SortableZoneList>
  )
}
