import styled from 'styled-components'

interface IFlexColumnProps {
  fixedWidth?: boolean
  width?: string
  widthGrowIndex?: number
  padding?: string
  margin?: string
}

export const FlexColumn = styled.div`
  flex: ${getFlexValue};
  padding: ${getPadding};
  margin: ${getMargin};
`

function getFlexValue({
  fixedWidth = false,
  width,
  widthGrowIndex = 0,
}: IFlexColumnProps) {
  return fixedWidth ? (width ? `0 0 ${width}` : '0 0') : `${widthGrowIndex} 0`
}

function getPadding({ padding = '0.25rem' }: IFlexColumnProps) {
  return padding
}

function getMargin({ margin = '' }: IFlexColumnProps) {
  return margin
}
