import { QZoneColorsZoneColors, useQZoneColors } from '../../../graph/generated'
import { useLoadingCache } from '../../../hooks/useLoadingCache'

const EMPTY_COLORS = []

export function useZoneColors() {
  const { data, loading } = useQZoneColors({}, { fetchPolicy: 'cache-first' })
  const getColors = useLoadingCache<RoA<QZoneColorsZoneColors>>()
  return getColors(loading, () => data.zoneColors) || EMPTY_COLORS
}
