import { t } from '@lingui/macro'
import { XFieldName } from '@speedlo/xform'
import { observer } from 'mobx-react-lite'
import React from 'react'

import { StyledFormInput } from '../../../atoms/FormInputStyled'
import { FormDisplayValue } from '../../../components/forms/FormDisplayValue'
import { useFormInputProps } from '../../../components/forms/FormInput'
import { i18n } from '../../../i18n'

interface IProps {
  fieldName: XFieldName
  asInfinity?: boolean
}

export const ZoneFormToPrice = observer<IProps, HTMLInputElement>(
  ({ fieldName, asInfinity = false }, ref) => {
    const fieldInputProps = useFormInputProps({
      fieldName,
    })()

    const inputProps = {
      ...fieldInputProps,
      className: 'toPrice',
      ref,
    }

    const label = (
      <label className="toPriceLabel" htmlFor={fieldInputProps.id}>
        {i18n._(
          /*i18n: Label for input with upper bound of order price where zone rule is applied */ t`to`,
        )}
      </label>
    )

    if (asInfinity) {
      return (
        <>
          {label}
          <StyledFormInput
            {...inputProps}
            value=""
            disabled
            placeholder="&infin;"
          />
        </>
      )
    }

    return (
      <>
        {label}
        <StyledFormInput {...inputProps} />
        <FormDisplayValue fieldName="currencyCode" />
      </>
    )
  },
  { forwardRef: true },
)
