import React, { useEffect } from 'react'

interface IProps {
  isShow: boolean
  confirmText: string
  onConfirm(): void
  onCancel(): void
}

export const ConfirmDialog: React.FC<IProps> = ({
  isShow,
  confirmText,
  onCancel,
  onConfirm,
}) => {
  useEffect(() => {
    if (isShow) {
      if (window.confirm(confirmText)) {
        onConfirm()
      } else {
        onCancel()
      }
    }
  }, [confirmText, isShow, onCancel, onConfirm])
  return null
}
