import { Trans } from '@lingui/macro'
import { useXFormState } from '@speedlo/xform'
import { useObserver } from 'mobx-react-lite'
import React from 'react'

import { CanMutate } from '../../../atoms/CanMutate'
import { FlexColumn } from '../../../atoms/FlexColumn'
import { FlexRowSingle } from '../../../atoms/FlexRowSingle'
import { FormSubmitButton } from '../../../components/forms/FormSubmitButton'
import { i18n } from '../../../i18n'
import { TouchButton } from '../../../molecules/TouchButton'
import { Colors } from '../../../styling'
import { useZoneRouteControl } from '../hooks/useZoneRouteControl'

interface IProps {}

export const ZoneFormControls: React.FC<IProps> = () => {
  const state = useXFormState()
  const { routeToZoneList } = useZoneRouteControl()
  return useObserver(() => (
    <FlexRowSingle>
      <FlexColumn widthGrowIndex={0.4} padding="0">
        <TouchButton
          color={Colors.greyMiddle}
          text={i18n.t`Back`}
          onClick={routeToZoneList}
          disabled={state.isSubmitting}
        />
      </FlexColumn>
      <FlexColumn widthGrowIndex={0.4} padding="0">
        <CanMutate mutationName="updateZone">
          <FormSubmitButton>
            <Trans>Save zone</Trans>
          </FormSubmitButton>
        </CanMutate>
      </FlexColumn>
    </FlexRowSingle>
  ))
}
