import { Trans } from '@lingui/macro'
import { idPropEq } from '@speedlo/tools'
import { observer } from 'mobx-react-lite'
import React, { Fragment } from 'react'
import { BulletList } from 'react-content-loader'

import { CardListTitle } from '../../../atoms/CardListTitle'
import { ZoneTypeEnum } from '../../../graph/generated'
import { ZoneListCard } from '../atoms/ZoneListCard'
import { ZoneListCardGroup } from '../atoms/ZoneListCardGroup'
import { useZoneListControl } from '../hooks/useZoneListControl'
import { useZoneModel } from '../hooks/useZoneModel'
import { useZoneRouteControl } from '../hooks/useZoneRouteControl'
import { TBranch, TZoneInList } from '../zones.types'

interface IProps {
  type: ZoneTypeEnum
}

export const ZoneList = observer<IProps>(({ type }) => {
  const zoneModel = useZoneModel()
  const { routeToZoneEdit } = useZoneRouteControl()
  const { isLoading, zonesByBranch, onZoneSort } = useZoneListControl(type)

  if (isLoading === null) {
    return <BulletList primaryOpacity={0.2} secondaryOpacity={0.2} />
  }

  if (zonesByBranch === null) {
    return (
      <CardListTitle>
        <Trans>Select brand to see its zones</Trans>
      </CardListTitle>
    )
  }

  const branches = zoneModel.root.user.filterBranches(
    zoneModel.branchFilterPredicate,
  )

  const renderZone = (zone: TZoneInList, idx: number) => (
    <ZoneListCard
      key={zone.id}
      type={type}
      zone={zone}
      branch={branches.find(idPropEq(zone.companyBranch.id))!}
      onEdit={() => routeToZoneEdit(zone.id)}
      index={idx}
      collection={zone.companyBranch.id}
    />
  )

  const renderBranch = (branch: TBranch, branchZones: RoA<TZoneInList>) => (
    <Fragment key={branch.id}>
      <CardListTitle>{branch.name}</CardListTitle>
      {branchZones.map(renderZone)}
    </Fragment>
  )

  return (
    <ZoneListCardGroup type={type} onSort={onZoneSort}>
      {branches.map(branch => {
        const branchZones = zonesByBranch[branch.id]
        return branchZones ? renderBranch(branch, branchZones) : null
      })}
    </ZoneListCardGroup>
  )
})
