import React from 'react'
import { Route, Switch } from 'react-router-dom'

import { NotFoundPage } from '../../../pages/NotFoundPage'
import { ROUTE_SETTINGS_ZONES } from '../../settings/settings.routes'
import { ZonesOverviewPage } from '../pages/ZonesOverviewPage'

interface IProps {}

export const ZonesRouting: React.FC<IProps> = () => (
  <Switch>
    <Route
      path={`${ROUTE_SETTINGS_ZONES}/:zoneId?`}
      component={ZonesOverviewPage}
    />
    <Route component={NotFoundPage} />
  </Switch>
)

export default ZonesRouting
