import { RM } from '@speedlo/tools'
import { useObjectValidation, XFormRender } from '@speedlo/xform'
import React from 'react'

import { useFormLoader } from '../../../components/forms/useFormLoader'
import {
  InputZoneUpdate,
  useMZoneUpdate,
  useQZoneForm,
  ZoneTypeEnum,
} from '../../../graph/generated'
import { parseNumber } from '../../../helpers/form'
import { useLoadingCache } from '../../../hooks/useLoadingCache'
import { useSnackBar } from '../../../hooks/useSnackBar'
import { i18n } from '../../../i18n'
import { useZoneRouteControl } from '../hooks/useZoneRouteControl'
import {
  TZoneFormPriceRule,
  TZoneFormValues,
  TZoneInForm,
} from '../zones.types'

interface IProps {
  zoneId: MaybeID
}

export const ZoneFormLoader: React.FC<IProps> = ({ children, zoneId }) => {
  const { routeToZoneList } = useZoneRouteControl()
  const { enqueueSuccess } = useSnackBar()

  const [zoneUpdate] = useMZoneUpdate()

  const validationSchema = useObjectValidation<Partial<TZoneFormValues>>(
    yup => ({
      name: yup.string().required(),
      minimalOrderPrice: yup
        .number()
        .required()
        .min(0),
    }),
  )

  const onSubmit = async (values: TZoneFormValues) => {
    const result = await zoneUpdate({
      zoneId: values.id,
      update: getWriteVariables(values),
    })
    if (result) {
      routeToZoneList()
      enqueueSuccess(i18n.t`Zone has been updated`)
    }
  }

  const xform = useFormLoader({
    initialValues: getInitialValues(),
    onSubmit,
    validationSchema,
  })

  const skip = !zoneId
  const { data, loading } = useQZoneForm({ zoneId: zoneId! }, { skip })

  const getZone = useLoadingCache<TZoneFormValues | null>(skip)
  const zone = getZone(loading, () =>
    skip ? null : normalizeZoneToValues(data.zone),
  )

  // FIXME: Not working because ApolloDefender swallows everything when error happens
  // React.useEffect(() => {
  //   if (zoneId && !loading && !zone) {
  //     routeToZoneList()
  //     return
  //   }
  // }, [loading, routeToZoneList, zone, zoneId])

  React.useEffect(() => {
    if (zone) {
      xform.api.mergeValues(zone)
      xform.state.pristineValues = zone
    }
    return () => {
      xform.api.resetForm()
    }
  }, [zone, xform.api, xform.state])

  return <XFormRender xform={xform}>{children as ReactNodes}</XFormRender>
}

function getInitialValues(): TZoneFormValues {
  return {
    id: '',
    type: ZoneTypeEnum.Area,
    name: '',
    enabled: true,
    branchId: '',
    zoneColor: '',
    geoPolygon: [],
    minimalPrice: 0,
    priceRules: {},
    priceRulesOrder: [],
    currencyCode: '',
  }
}

const withoutTypeName = RM.omit(['__typename'])

function normalizeZoneToValues(zone: TZoneInForm): TZoneFormValues {
  return {
    id: zone.id,
    type: zone.zoneType.enum,
    name: zone.name,
    enabled: zone.enabled,
    zoneColor: zone.zoneColor.id,
    branchId: zone.branchWithCurrency.id,
    geoPolygon: zone.geoPolygon.map(withoutTypeName),
    currencyCode: zone.branchWithCurrency.currency.code,
    minimalPrice: zone.minimalOrderPrice.value,
    ...normalizePriceRules(zone),
  }
}

function normalizePriceRules(zone: TZoneInForm) {
  // it's neccessary to add temporary ID for the collection to be modified safely
  const indexedPriceRules: TZoneFormPriceRule[] = zone.priceRangeRules.map(
    (rule, idx) => ({
      clientId: String(idx + 1),
      toPrice: rule.toPrice.value,
      price: rule.price.value,
    }),
  )
  return {
    priceRulesOrder: indexedPriceRules.map(RM.prop('clientId')),
    priceRules: indexedPriceRules.reduce((acc, rule) => {
      acc[rule.clientId] = rule
      return acc
    }, {}),
  }
}

function getWriteVariables(values: TZoneFormValues): InputZoneUpdate {
  return {
    name: values.name,
    enabled: values.enabled,
    companyBranch: values.branchId,
    zoneColor: values.zoneColor,
    geoPolygon: values.geoPolygon,
    minimalOrderPrice: {
      value: Number(values.minimalPrice),
    },
    priceRangeRules: values.priceRulesOrder.map((clientId, idx) =>
      normalizePriceRuleToWrite(
        values.priceRules[clientId],
        idx === values.priceRulesOrder.length - 1,
      ),
    ),
  }
}

function normalizePriceRuleToWrite(rule: TZoneFormPriceRule, isLast: boolean) {
  return {
    price: { value: parseNumber(rule.price!) },
    toPrice: { value: isLast ? -1 : parseNumber(rule.toPrice!) },
  }
}
