import { RM } from '@speedlo/tools'
import { XFieldName } from '@speedlo/xform'
import React from 'react'
import styled from 'styled-components'

import { FormDropdown } from '../../../components/forms/FormDropdown'
import { QZoneColorsZoneColors } from '../../../graph/generated'
import { Colors } from '../../../styling'
import { useZoneColors } from '../hooks/useZoneColors'

interface IProps {
  fieldName: XFieldName
}

const ColorOption = styled.div`
  background-color: ${({ color }: { color: string }) => color};
  color: ${Colors.white};
  width: 6rem;
  padding: 0.4rem 1rem;
  margin: 0;
  height: 100%;
`

const makeStyles = () => ({
  option: base => ({
    ...base,
    padding: 0,
    margin: 0,
  }),
  menu: base => ({
    ...base,
    width: '8rem',
    maxWidth: '8rem',
  }),
})

export const ZoneColorSelector: React.FC<IProps> = ({ fieldName }) => {
  const zoneColors = useZoneColors()
  return (
    <FormDropdown<QZoneColorsZoneColors>
      fieldName={fieldName}
      options={zoneColors}
      isLoading={zoneColors.length === 0}
      width={8}
      styles={makeStyles()}
      defaultValue={RM.head(zoneColors)}
      getOptionValue={RM.prop('id')}
      formatOptionLabel={zoneColor => (
        <ColorOption color={zoneColor.color}>{zoneColor.code}</ColorOption>
      )}
    />
  )
}
