import { Trans } from '@lingui/macro'
import React from 'react'
import styled from 'styled-components/macro'

import { ZoneTypeEnum } from '../../../graph/generated'
import { Colors } from '../../../styling'

interface IProps {
  type: ZoneTypeEnum
}

const StyledZoneListSection = styled.p`
  color: ${Colors.greyMiddle};
`

export const ZoneListSection: React.FC<IProps> = ({ type }) => {
  if (type === ZoneTypeEnum.Return) {
    return (
      <StyledZoneListSection>
        <Trans>
          Notification will be shown when driver enters one of these zones.
        </Trans>
      </StyledZoneListSection>
    )
  }
  return null
}
