import { RM } from '@speedlo/tools'
import React from 'react'
import { SortEndHandler } from 'react-sortable-hoc'

import { useMZoneUpdatePriority, ZoneTypeEnum } from '../../../graph/generated'
import { TZoneInList } from '../zones.types'
import { useZoneListQuery } from './useZoneListQuery'

const sortZones = RM.sortWith<TZoneInList>([
  RM.ascend(RM.path(['companyBranch', 'name'])),
  RM.descend(RM.prop('priority')),
])

const groupByBranch = RM.groupBy((zone: TZoneInList) => zone.companyBranch.id)
const isZoneType = RM.pathEq(['zoneType', 'enum'])

export function useZoneListControl(type: ZoneTypeEnum) {
  const { zones, loading } = useZoneListQuery()

  const zonesByBranch = React.useMemo(() => {
    if (!zones) {
      return null
    }
    return RM.mapObjIndexed(
      sortZones,
      groupByBranch(zones.filter(isZoneType(type))),
    )
  }, [type, zones])

  const [updatePriority] = useMZoneUpdatePriority()

  const onZoneSort: SortEndHandler = React.useCallback(
    ({ oldIndex, newIndex, collection }) => {
      if (!zonesByBranch) {
        return
      }
      const group = zonesByBranch[collection]
      const zone = group[oldIndex]

      const offset = oldIndex - newIndex

      updatePriority(
        {
          zoneId: zone.id,
          offset,
        },
        {
          optimisticResponse: {
            __typename: 'Mutation',
            updateZonePriority: [
              {
                __typename: 'Zone',
                id: zone.id,
                priority: zone.priority + offset,
              },
            ],
          },
        },
      )
    },
    [zonesByBranch, updatePriority],
  )

  return { isLoading: loading, zonesByBranch, onZoneSort }
}
