import { useRouterContext } from '@speedlo/hooks'
import React from 'react'

import { useRoot } from '../../../atoms/RootProvider'
import { ROUTE_SETTINGS_ZONES } from '../../settings/settings.routes'

export function useZoneRouteControl() {
  const { user } = useRoot()
  const { history } = useRouterContext()
  const getRouteToZone = React.useCallback((zoneId: ID) => {
    return `${ROUTE_SETTINGS_ZONES}/${zoneId}`
  }, [])

  return {
    getRouteToZone,
    routeToZoneEdit: React.useCallback(
      (editZoneId: ID) => {
        if (user.canQuery('zone')) {
          history.push(getRouteToZone(editZoneId))
        }
      },
      [getRouteToZone, history, user],
    ),
    routeToZoneList: React.useCallback(() => {
      history.push(ROUTE_SETTINGS_ZONES)
    }, [history]),
  }
}
