import { XFieldPath } from '@speedlo/xform'
import { observer } from 'mobx-react-lite'
import React from 'react'

import { ZoneFormForPrice } from './ZoneFormForPrice'
import { ZoneFormToPrice } from './ZoneFormToPrice'

interface IProps {
  fieldPath: XFieldPath
  shouldFocus: boolean
  isLastRule: boolean
}

export const ZoneFormRuleInputs = observer<IProps>(
  ({ fieldPath, shouldFocus, isLastRule }) => {
    const toPriceRef = React.useRef<HTMLInputElement>(null)
    const forPriceRef = React.useRef<HTMLInputElement>(null)

    React.useEffect(() => {
      if (!shouldFocus) {
        return
      }
      if (toPriceRef.current && !isLastRule) {
        toPriceRef.current.focus()
      } else if (forPriceRef.current) {
        forPriceRef.current.focus()
      }
    }, [shouldFocus, isLastRule])

    return (
      <>
        <div className="toPriceField">
          <div className="verticalLine"></div>
          <ZoneFormToPrice
            fieldName={[...fieldPath, 'toPrice']}
            ref={toPriceRef}
            asInfinity={isLastRule}
          />
        </div>
        <div className="forPriceField">
          <ZoneFormForPrice
            fieldName={[...fieldPath, 'price']}
            ref={forPriceRef}
          />
        </div>
      </>
    )
  },
)
