import { observer } from 'mobx-react-lite'
import React from 'react'

import { useCanMutate } from '../hooks/useAccessGuard'
import { NoAccess } from './NoAccess'

interface IProps {
  mutationName: string
}

export const CanMutate = observer<IProps>(({ mutationName, children }) => {
  const canMutate = useCanMutate(mutationName)
  return canMutate ? (
    <>{children}</>
  ) : (
    <NoAccess>{`No access to mutation ${mutationName}`}</NoAccess>
  )
})
