import { Trans } from '@lingui/macro'
import {
  GoogleMap,
  useGoogleMap as useGoogleMapOrig,
  useLoadScript,
} from '@react-google-maps/api'
import { Sentry } from '@speedlo/sentry'
import { Observer } from 'mobx-react-lite'
import React, { useContext } from 'react'

import { appConfig } from '../config'
import { TMapModel } from '../models/MapModel'
import { Radiuses } from '../styling'
import { useRoot } from './RootProvider'

interface IBaseMapProps {
  model: TMapModel
}

const mapModelContext = React.createContext<TMapModel | null>(null)

const libraries = ['places']

export const BaseMap: React.FC<IBaseMapProps> = ({ model, children }) => {
  const options: google.maps.MapOptions = {
    streetViewControl: false,
    mapTypeControl: false,
    fullscreenControl: false,
  }

  const { user } = useRoot()
  const { isLoaded, loadError, url: googleMapUrl } = useLoadScript({
    id: 'script-loader',
    version: 'weekly',
    googleMapsApiKey: appConfig.googleMapsApi,
    libraries,
    language: user.language!,
  })

  const renderMap = () => (
    <Observer>
      {() => (
        <GoogleMap
          zoom={model.zoom}
          center={model.center}
          onCenterChanged={model.updatePosition}
          onZoomChanged={model.updateZoom}
          onLoad={model.setMapRef}
          options={options}
          mapContainerStyle={{
            borderRadius: Radiuses.Card,
            overflow: 'hidden',
            height: '100%',
          }}
        >
          {children}
        </GoogleMap>
      )}
    </Observer>
  )

  const googleAvailable = Reflect.has(window, 'google')

  if (loadError || (isLoaded && !googleAvailable)) {
    Sentry.withScope(scope => {
      scope.setExtras({ googleMapUrl })
      Sentry.captureException(loadError)
    })
    return (
      <h2>
        <Trans>Cannot load map right now. Please try again later.</Trans>
      </h2>
    )
  }

  return isLoaded ? renderMap() : null
}

export function useMapModel() {
  const model = useContext(mapModelContext)
  if (!model) {
    throw new Error('Trying to useMapModel, but BaseMap is missing in the tree')
  }
  return model
}

export function useGoogleMap() {
  const map = useGoogleMapOrig()
  if (!map) {
    throw new Error(
      'Trying to useGoogleMap, but BaseMap is missing in the tree',
    )
  }
  return map
}
