import { Edit } from '@material-ui/icons'
import Color from 'color'
import React from 'react'
import { SortableElement } from 'react-sortable-hoc'
import styled from 'styled-components'

import { Card } from '../../../atoms/Card'
import { DragHandle } from '../../../atoms/DragHandle'
import { FlexColumn } from '../../../atoms/FlexColumn'
import { FlexRowSingle } from '../../../atoms/FlexRowSingle'
import { SizerButton } from '../../../atoms/SizerButton'
import {
  FCompanyBranchFragment,
  FZoneFragment,
  ZoneTypeEnum,
} from '../../../graph/generated'
import { TouchButton } from '../../../molecules/TouchButton'
import { Colors, Spacing } from '../../../styling'

interface IProps {
  type: ZoneTypeEnum
  zone: FZoneFragment
  branch: FCompanyBranchFragment
  onEdit(): void
}

interface IStyledProps {
  type: ZoneTypeEnum
  color: string
}

const StyledZoneListCard = styled(Card)<IStyledProps>`
  background: ${getBackgroundColor};
`

function getBackgroundColor({ color, type }: IStyledProps) {
  if (type === ZoneTypeEnum.Area) {
    return ''
  }
  const baseColor = Color(color)
    .alpha(0.8)
    .darken(0.2)
  const lighterColor = baseColor.lighten(0.4)
  return `repeating-linear-gradient(135deg, ${baseColor}, ${baseColor} 10px, ${lighterColor} 10px, ${lighterColor} 20px)`
}

const ZoneNameStyled = styled.div`
  font-size: 1.125rem;
  margin-top: 0.2rem;
`

const ZoneDescriptionStyled = styled.div`
  font-size: 0.875rem;
`
const ZoneFlexRowStyled = styled(FlexRowSingle)`
  height: ${Spacing.ButtonHeight + Spacing.DefaultSizeUnit};
`

export const ZoneListCard = SortableElement(
  ({ zone, branch, type, onEdit }: IProps) => (
    <StyledZoneListCard
      key={zone.id}
      type={type}
      padding={0}
      color={zone.enabled ? zone.zoneColor.color : Colors.greyMiddle}
      onClick={onEdit}
    >
      <ZoneFlexRowStyled>
        <FlexColumn fixedWidth padding="0 0.5rem 0 0">
          <SizerButton>
            <TouchButton icon={Edit} />
          </SizerButton>
        </FlexColumn>
        <FlexColumn widthGrowIndex={1}>
          <ZoneNameStyled>
            {zone.name}
            {` [P${zone.priority}]`}
          </ZoneNameStyled>
          <ZoneDescriptionStyled>{branch.name}</ZoneDescriptionStyled>
        </FlexColumn>
        <FlexColumn padding="0">
          <DragHandle />
        </FlexColumn>
      </ZoneFlexRowStyled>
    </StyledZoneListCard>
  ),
)
