import { idPropEq } from '@speedlo/tools'
import { useXField, useXFieldState } from '@speedlo/xform'
import { observer } from 'mobx-react-lite'
import React, { useCallback } from 'react'

import { BaseMap } from '../../../atoms/BaseMap'
import { FCompanyBranchFragment } from '../../../graph/generated'
import { MapCompanyBranchMarker } from '../../../molecules/MapCompanyBranchMarker'
import { Colors } from '../../../styling'
import { ZoneMapArea } from '../atoms/ZoneMapArea'
import { ZoneMapEditableArea } from '../atoms/ZoneMapEditableArea'
import { useZoneColors } from '../hooks/useZoneColors'
import { useZoneMapControl } from '../hooks/useZoneMapControl'
import { useZoneModel } from '../hooks/useZoneModel'
import { useZoneRouteControl } from '../hooks/useZoneRouteControl'
import { TZoneFormValues, TZoneInList } from '../zones.types'

interface IProps {
  zoneId: MaybeID
}

export const ZoneMap = observer<IProps>(({ zoneId }) => {
  const zoneModel = useZoneModel()

  const { isActive, branches, zones } = useZoneMapControl(zoneId)
  const { routeToZoneEdit } = useZoneRouteControl()

  const renderZoneArea = (zone: TZoneInList) => {
    if (zone.id === zoneId) {
      return null
    }

    return (
      <ZoneMapArea
        key={`zone${zone.id}`}
        type={zone.zoneType.enum}
        points={zone.geoPolygon}
        priority={zone.priority}
        color={
          isActive(zone.companyBranch.id) && zone.enabled && zoneId === null
            ? zone.zoneColor.color
            : Colors.greyMiddle
        }
        onFocus={() => {
          if (isActive(zone.companyBranch.id)) {
            routeToZoneEdit(zone.id)
          }
        }}
      />
    )
  }

  const polygonField = useXField<TZoneFormValues['geoPolygon']>('geoPolygon')
  const typeField = useXFieldState<TZoneFormValues['type']>('type')
  const colorField = useXFieldState<TZoneFormValues['zoneColor']>('zoneColor')
  const zoneColors = useZoneColors()

  const renderEditZone = () => {
    if (polygonField.value.length === 0) {
      return null
    }
    const zoneColor = zoneColors.find(idPropEq(colorField.value))
    return (
      <ZoneMapEditableArea
        key={`zone${zoneId}`}
        type={typeField.value}
        color={zoneColor ? zoneColor.color : ''}
        initialPoints={polygonField.value}
        onChange={polygonField.setValue}
      />
    )
  }

  const renderBranchLabel = useCallback(
    (branch: FCompanyBranchFragment) => (
      <MapCompanyBranchMarker
        key={`branch${branch.id}`}
        branch={branch}
        isActive={isActive(branch.id)}
      />
    ),
    [isActive],
  )

  return (
    <BaseMap model={zoneModel.map}>
      {zones && zones.map(renderZoneArea)}
      {zoneId && renderEditZone()}
      {branches && branches.map(renderBranchLabel)}
    </BaseMap>
  )
})
